:root {
  --color-primary: #17479e;
  --color-white: #ffffff;
  --color-black: #000000;
  --color-black-2: #363535;
  --color-gray-1: #333333;
  --color-gray-2: #4f4f4f;
  --color-gray-3: #f5f5f5;
  --color-gray-4: #f8f8f8;
  --color-gray-5: #e0e0e0;
  --color-gray-light: #f2f2f2;
  --color-gray-dark: #535e76;
  --font-family: "Poppins", sans-serif !important;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
}
@media screen and (max-width: 990px) {
  html {
    font-size: 55%;
  }
}
@media screen and (max-width: 768px) {
  html {
    font-size: 52%;
  }
}

body {
  font-size: 1.6rem;
  line-height: 2.4rem;
  box-sizing: border-box;
}

body,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--font-family);
}

ul {
  margin: 0;
  padding: 0;
}

a {
  color: #000;
  text-decoration: none;
}
a:hover {
  color: unset;
}

img {
  width: 100%;
}

.fw-100 {
  font-weight: 100 !important;
}

.fw-200 {
  font-weight: 200 !important;
}

.fw-300 {
  font-weight: 300 !important;
}

.fw-400 {
  font-weight: 400 !important;
}

.fw-500 {
  font-weight: 500 !important;
}

.fw-600 {
  font-weight: 600 !important;
}

.fw-700 {
  font-weight: 700 !important;
}

.fw-800 {
  font-weight: 800 !important;
}

.fw-900 {
  font-weight: 900 !important;
}

.fz10 {
  font-size: rem(10);
}

.fz11 {
  font-size: rem(11);
}

.fz12 {
  font-size: rem(12);
}

.fz13 {
  font-size: rem(13);
}

.fz14 {
  font-size: rem(14);
}

.fz15 {
  font-size: rem(15);
}

.fz16 {
  font-size: rem(16);
}

.fz18 {
  font-size: rem(18);
}

.fz19 {
  font-size: rem(19);
}

.fz20 {
  font-size: rem(20);
}

.fz22 {
  font-size: rem(22);
}

.fz24 {
  font-size: rem(24);
  line-height: 30px;
}

.fz28 {
  font-size: rem(28);
  line-height: 30px;
}

.fz44 {
  font-size: rem(44);
}

.fz52 {
  font-size: rem(52);
}

.fz64 {
  font-size: rem(64);
}

/*--------------------------------------------------------------
#1.3 Animations
--------------------------------------------------------------*/
.custom-container {
  max-width: 111rem;
  margin: 0 auto;
  padding: 0 1rem;
}

.container-padding {
  padding: 10.12rem 0px;
}
@media screen and (max-width: 768px) {
  .container-padding {
    flex-direction: column;
    padding: 5rem 0rem;
  }
}

.container-padding-2 {
  padding: 9.5rem 0px;
}
@media screen and (max-width: 768px) {
  .container-padding-2 {
    flex-direction: column;
    padding: 5rem 0rem;
  }
}

/*--------------------------------------------------------------
# Theme Colors
--------------------------------------------------------------*/
.c-mt-6 {
  margin-top: 6px;
}

.c-mr-6 {
  margin-right: 6px;
}

.c-mb-6 {
  margin-bottom: 6px;
}

.c-ml-6 {
  margin-left: 6px;
}

.uppercase {
  text-transform: uppercase;
}

.bg {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.text-center {
  text-align: center;
}

.m-x-auto {
  margin-left: auto;
  margin-right: auto;
}

.fit-content {
  width: fit-content;
  height: auto;
}

.scrollbar {
  padding-left: 10px;
  overflow-y: auto;
  cursor: s-resize;
}
.scrollbar::-webkit-scrollbar-track {
  border-radius: 3px;
  background-color: #f5f5f5;
  opacity: 0.1;
  cursor: pointer;
}
.scrollbar::-webkit-scrollbar {
  width: 4px;
  background-color: #f5f5f5;
}
.scrollbar::-webkit-scrollbar-thumb {
  border-radius: 3px;
  background-color: red;
}

.bt-btn {
  background-color: var(--color-primary) !important;
  font-size: 1.3rem;
  color: var(--color-white) !important;
  border: none;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-weight: 500;
  padding: 0.8rem 2.1rem;
  text-decoration: none;
  transition: 0.2s;
}
.bt-btn:hover {
  opacity: 0.9;
}
.bt-btn:focus, .bt-btn:active, .bt-btn:focus-visible {
  outline: none;
  box-shadow: none !important;
  border: none;
}
.bt-btn.h-34 {
  height: 3.4rem;
}
.bt-btn.h-48 {
  height: 4.8rem;
}
.bt-btn.h-58 {
  height: 5.8rem;
}
.bt-btn.btn-primary {
  padding: 1.2rem 2.4rem;
  font-weight: 600;
  font-size: 1.6rem;
}
.bt-btn.btn-secondary {
  background-color: var(--color-white) !important;
  color: var(--color-primary) !important;
  font-weight: 600;
  font-size: 1.8rem;
  padding: 1.6rem 3.2rem;
}

.header-top-werapper {
  display: flex;
  justify-content: space-between;
}
@media screen and (max-width: 768px) {
  .header-top-werapper {
    flex-wrap: wrap;
    justify-content: center;
  }
}
.header-top-werapper .logo {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 2.4rem;
  padding-bottom: 2.4rem;
}
.header-top-werapper .logo img {
  width: auto;
}
.header-top-werapper .top-banner {
  padding-top: 2.4rem;
  padding-bottom: 2.4rem;
  padding-left: 1rem;
  padding-right: 1rem;
}
.header-top-werapper .top-right-wrapper {
  display: flex;
  padding-top: 1.8rem;
  padding-bottom: 1.8rem;
  align-items: flex-end;
}
.header-top-werapper .top-right-wrapper button,
.header-top-werapper .top-right-wrapper .social-icon {
  margin-right: 6px;
  margin-bottom: 6px;
}
.header-top-werapper .social-icon ul {
  display: flex;
}
.header-top-werapper .social-icon ul li {
  list-style: none;
}
.header-top-werapper .social-icon ul li:not(:last-child) {
  padding-right: 4px;
}

.header-banner {
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  z-index: 1;
}
.header-banner::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--color-black-2);
  opacity: 0.3;
  z-index: -1;
}
.header-banner .menu ul {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}
.header-banner .menu ul li {
  list-style: none;
}
.header-banner .menu ul li a {
  text-decoration: none;
  font-size: 1.6rem;
  font-weight: 400;
  color: var(--color-white);
  padding: 1.6rem;
  display: flex;
  transition: 0.2s;
}
.header-banner .menu ul li a:active, .header-banner .menu ul li a:focus {
  text-decoration: underline;
}
.header-banner .menu ul li:hover a {
  text-decoration: underline;
}
.header-banner .menu ul li:not(:last-child) {
  margin-right: 2rem;
}
.header-banner .banner-content {
  display: flex;
  align-items: center;
  padding: 10.18rem 1rem;
}
.header-banner .banner-content.small {
  padding: 20px 0px 60px;
}
@media screen and (max-width: 990px) {
  .header-banner .banner-content.small {
    padding: 5rem 1rem;
  }
}
@media screen and (max-width: 990px) {
  .header-banner .banner-content {
    flex-direction: column;
    padding: 5rem 1rem;
  }
}
@media screen and (max-width: 768px) {
  .header-banner .banner-content {
    padding: 3rem 1rem;
  }
}
.header-banner .calender {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  min-height: 19.1rem;
  min-width: 10.13rem;
  padding: 3.2rem 1.3rem;
  border: 1px solid #fff;
  border-radius: 2.1rem;
}
@media screen and (max-width: 990px) {
  .header-banner .calender {
    margin-bottom: 3rem;
  }
}
@media screen and (max-width: 768px) {
  .header-banner .calender {
    min-height: 10rem;
    margin-bottom: 3rem;
  }
}
.header-banner .calender span {
  font-weight: 600;
  color: var(--color-white);
}
.header-banner .calender .date {
  font-size: 4rem;
  padding-bottom: 0.7rem;
}
@media screen and (max-width: 768px) {
  .header-banner .calender .date {
    font-size: 3rem;
  }
}
.header-banner .calender .month {
  font-size: 2.4rem;
  border-bottom: 1px solid var(--color-white);
  padding: 5px 2rem 1.2rem 2rem;
}
@media screen and (max-width: 768px) {
  .header-banner .calender .month {
    font-size: 2rem;
  }
}
.header-banner .calender .year {
  font-weight: 500;
  font-size: 2rem;
  padding: 1.5rem 1rem;
}
@media screen and (max-width: 768px) {
  .header-banner .calender .year {
    font-size: 1.5rem;
  }
}
.header-banner .banner-text {
  padding-left: 2.4rem;
}
@media screen and (max-width: 768px) {
  .header-banner .banner-text {
    padding: 1rem;
  }
}
.header-banner .banner-title {
  text-transform: uppercase;
  font-size: 6rem;
  color: var(--color-white);
  font-weight: 700;
  line-height: 7.2rem;
  margin-bottom: 1.8rem;
  text-transform: uppercase;
}
@media screen and (max-width: 768px) {
  .header-banner .banner-title {
    font-size: 3rem;
    line-height: 3.5rem;
  }
}
.header-banner .banner-sub-title {
  font-size: 2rem;
  font-weight: 500;
  color: var(--color-white);
  line-height: 3rem;
  max-width: 50.5rem;
  margin-bottom: 3.2rem;
}
@media screen and (max-width: 768px) {
  .header-banner .banner-sub-title {
    font-size: 1.6rem;
    line-height: 2rem;
  }
}

.mobile-header-top {
  display: none;
}
@media screen and (max-width: 990px) {
  .mobile-header-top {
    display: block;
  }
}
.mobile-header-top .responsive-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.2rem;
}
.mobile-header-top .responsive-menu .logo img {
  width: 62px;
  height: 36px;
}
.mobile-header-top .responsive-menu .toggle-icon button {
  background-color: transparent;
  border: none;
  width: 36px;
}
.mobile-header-top .mobile-menu {
  margin-top: 12px;
  margin-bottom: 12px;
  height: 100vh;
}
.mobile-header-top .mobile-menu ul li {
  list-style: none;
  margin-bottom: 1.6rem;
  padding: 0 1.2rem;
}
.mobile-header-top .mobile-menu ul li a {
  font-size: 13px;
  font-weight: 500;
  line-height: 1.8rem;
  color: var(--color-black);
}
.mobile-header-top .mobile-menu .mobile-register-btn {
  margin: 0 12px;
}

@media screen and (max-width: 990px) {
  .menu,
.header-top-werapper {
    display: none;
  }
}
/*--------------------------------------------------------------
# Site Footer
--------------------------------------------------------------*/
.footer .footer-content {
  display: flex;
  padding-top: 10.12rem;
}
@media screen and (max-width: 990px) {
  .footer .footer-content {
    padding-bottom: 0rem;
    flex-wrap: wrap;
  }
}
.footer .footer-content .item {
  margin-bottom: 3rem;
}
.footer .footer-content .about {
  margin-right: 4rem;
  max-width: 25%;
  width: 100%;
}
@media screen and (max-width: 990px) {
  .footer .footer-content .about {
    max-width: 48%;
    margin-bottom: 3rem;
    margin-right: 3rem;
  }
}
@media screen and (max-width: 768px) {
  .footer .footer-content .about {
    max-width: 100%;
    margin-right: 0rem;
  }
}
.footer .footer-content .about img {
  width: 10.24rem;
}
.footer .footer-content .about p {
  font-size: 1.6rem;
  font-weight: 400;
  color: var(--color-black);
  padding: 1.5rem 0;
  letter-spacing: 0.3px;
}
.footer .footer-content .contact-us .title,
.footer .footer-content .quick .title,
.footer .footer-content .footer-social .title {
  font-size: 1.8rem;
  font-weight: 600;
  color: var(--color-gray-1);
  margin-bottom: 1.6rem;
}
.footer .footer-content .contact-us {
  margin-right: 4rem;
  max-width: 25%;
  width: 100%;
}
@media screen and (max-width: 990px) {
  .footer .footer-content .contact-us {
    max-width: 48%;
    margin-bottom: 3rem;
    margin-right: 0rem;
  }
}
@media screen and (max-width: 768px) {
  .footer .footer-content .contact-us {
    max-width: 100%;
  }
}
.footer .footer-content .contact-us .label {
  font-size: 1.8rem;
  font-weight: 400;
  color: var(--color-gray-2);
  margin-bottom: 8px;
}
.footer .footer-content .contact-us li {
  list-style: none;
  margin-bottom: 1.6rem;
}
.footer .footer-content .contact-us li span {
  font-size: 2rem;
  font-weight: 400;
  color: var(--color-black);
  line-height: 3rem;
}
.footer .footer-content .quick {
  margin-right: 4rem;
  max-width: 25%;
  width: 100%;
}
@media screen and (max-width: 990px) {
  .footer .footer-content .quick {
    margin-right: 3rem;
    max-width: 48%;
  }
}
@media screen and (max-width: 768px) {
  .footer .footer-content .quick {
    margin-right: 0rem;
    max-width: 48%;
  }
}
.footer .footer-content .footer-social {
  margin-right: 4rem;
  max-width: 25%;
  width: 100%;
}
@media screen and (max-width: 990px) {
  .footer .footer-content .footer-social {
    margin-right: 0rem;
    max-width: 48%;
  }
}
@media screen and (max-width: 768px) {
  .footer .footer-content .footer-social {
    margin-right: 0rem;
    max-width: 48%;
  }
}
.footer .footer-content .quick li,
.footer .footer-content .footer-social li {
  list-style: none;
  margin-bottom: 1.6rem;
}
.footer .footer-content .quick li a,
.footer .footer-content .footer-social li a {
  font-size: 1.8rem;
  font-weight: 400;
  color: var(--color-gray-1);
  line-height: 2rem;
}
.footer .all-rights {
  border-top: 1px solid var(--color-gray-5);
  padding: 2rem 0rem 4.6rem 0rem;
  font-size: 1.6rem;
  font-weight: 400;
  color: var(--color-gray-1);
  line-height: 2rem;
}
@media screen and (max-width: 990px) {
  .footer .all-rights {
    text-align: center;
  }
}